    IconLink,
    IconMyInfo,
    IconHome,
    IconCall,
    IconCalculate,
    IconBusinessFirst,
    RoundStatus, //240703 
  },
  setup() {
    const layerGlobalNav = inject('layerGlobalNav', {});

    const store = {
      ui: {
        common: useUiCommonStore(),
      },
    };

    const isLoggedIn = ref(false); //    
    const isSalesperson = ref(false); //    

    const login = () => {
      //    , isLoggedIn   true .
      isLoggedIn.value = true;
      isSalesperson.value = true;
    };

    const logout = () => {
      //    , isLoggedIn   false .
      isLoggedIn.value = false;
      isSalesperson.value = false;
    };

    const routerLinkClick = () => {
      if (
        layerGlobalNav.layer &&
        layerGlobalNav.layer.value &&
        layerGlobalNav.layer.value.close
      ) {
        layerGlobalNav.layer.value.close();
      }
    };

    return {
      store,
      isLoggedIn,
      isSalesperson,
      login,
      logout,
      routerLinkClick,
    };
  },
};
